
import { defineComponent, computed } from 'vue'
import { findComByName } from '@/data/system-components'

export default defineComponent({
  name: 'SettingPanel',
  props: {
    comName: {
      type: String,
      required: true,
    },
    comAlias: {
      type: String,
      required: true,
    },
  },
  setup(props) {
    const comTitle = computed(() => {
      const obj = findComByName(props.comName)
      return obj?.com.name
    })

    return {
      comTitle,
    }
  },
})
